.card.card-image-effect { background-size: cover; text-align: center; height: 13rem; position: relative; overflow: hidden; text-decoration: none; padding: 3rem; }

.card.card-image-effect .card-img { position: absolute; top: 0; left: 0; right: 0; bottom: 0; transform: scale(1); transition: transform 0.2s; }

.card.card-image-effect .card-img:after { position: absolute; top: 0; left: 0; right: 0; bottom: 0; display: block; content: ''; background-color: rgba(255, 255, 255, 0.8); opacity: 0; transition: opacity 0.15s; }

.card.card-image-effect > *:not(.card-img) { opacity: 0; transition: opacity 0.2s; position: relative; }

.card.card-image-effect:hover .card-img { transform: scale(1.1); }

.card.card-image-effect:hover .card-img:after { opacity: 1; }

.card.card-image-effect:hover > *:not(.card-img) { opacity: 1; }

.card.card-image-effect:hover, .card.card-image-effect:focus, .card.card-image-effect:active { text-decoration: none; }

@media (max-width: 767px) { .card.card-image-effect { height: auto; padding: 0; margin-bottom: 4rem; }
